<template>
  <div class="share-special-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view class="scroll-view" ref="scroll">
        <div class="container" @click="judgeGuide">
          <div class="special-top">
            <img class="top-bg" v-if="!special.bannerimg" src="./bg_top@2x.png"/>
            <img class="top-bg" v-if="special.bannerimg" :src="special.bannerimg"/>
            <!-- <img class="top-logo" :src="special.logo" @error="defaultLogo"/> -->
            <div class="special-msgs">
              <h1>{{ special.title }}</h1>
              <h3 v-if="special.count > 0">{{ `项目数量：${special.count}` }}</h3>
            </div>
          </div>
          <div class="special-desc" v-if="special.desc">
            {{ special.desc }}
            <span v-if="isShowBtn" class="special-show-btn" @click="showAll">{{ isShowAll ? '收起' : '展开' }}</span>
          </div>
          <ul class="special-project-list">
            <li class="item"
              v-for="item in list"
              :key="item.projectlibzj">
              <img class="logo" :src="item.logo"  @error="defaultLogo">
              <div class="name-wrapper clearfix">
                <span class="name">{{ item.name }}</span>
                <span class="new" v-if="item.weekflag && !item.viewflag"></span>
                <span class="float-right money">{{ item.money }}</span>
              </div>
              <div class="desc">{{ item.descriptionmemo }}</div>
              <div class="msgs clearfix">
                <span class="turn">{{ item.turn }}</span>
                <small v-if="item.fields">|</small>
                <span class="field">{{ item.fields }}</span>
                <div class="float-right identify">
                  <img class="badge" v-for="(icon, index) in item.iconList" :key="index" :src="icon.imgurl" />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </pem-scroll-view>
      <div class="footer-open" @click="judgeGuide">
        <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
        <h4>项目工场</h4>
        <p>找项目、找投资就到项目工场</p>
        <div class="btn">打开</div>
      </div>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'

import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'

export default {
  data () {
    return {
      rootImgUrl: ImgPath,
      showGuideMask: false,
      list: [],
      special: {
        title: '',
        desc: '',
        logo: null,
        bannerimg: null,
        count: 0
      },
      tempDesc: '',
      descLength: 66,
      isShowAll: false,
      isShowBtn: false
    }
  },
  components: {
    PemScrollView,
    PemButton
  },
  created () {
    this._initData()
    this.wxShare({})
  },
  methods: {
    _initData () {
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=4&id=${this.$route.query.id}`
      }
      this._getShareSpecial()
    },
    _getShareSpecial () {
      let options = {
        fromtype: '3',
        code: 'special_project',
        channelzj: this.$route.query.id
      }
      MiniSiteApi.getAppShareSpecial(options).then(data => {
        if (+data.resultcode === 1) {
          this.special.logo = data.logo ? (ImgPath + data.logo) : tempLogoUrl
          if (data.bannerimg) {
            this.special.bannerimg = ImgPath + data.bannerimg
          }
          this.special.desc = data.remark || ''
          this.tempDesc = data.remark || ''
          if (this.tempDesc.length <= this.descLength) {
            this.special.desc = this.tempDesc
          } else {
            this.special.desc = this.tempDesc.substr(0, this.descLength) + '...'
            this.isShowBtn = true
          }
          this.list = data.resultList.filter(item => {
            if (item.layout === 'banner') {
              this.special.title = item.specialname || ''
              this.special.count = item.projectcount || 0
              setTitle(this.special.title)
              this.wxShare({
                title: this.special.title,
                content: this.special.desc,
                imgUrl: this.special.logo
              })
              return false
            }
            if (item.layout === 'project') {
              item.logo = item.logo ? (ImgPath + item.logo) : tempLogoUrl
              item.money = '暂不透露'
              if (!item.raisefundstatus && item.raisefunds && item.currencytype && item.raisefunds > 0) {
                item.money = item.currencytype + item.raisefunds + '万'
              }
              if (item.iconList) {
                item.iconList = item.iconList.map(icon => {
                  icon.imgurl = ImgPath + icon.imgurl
                  return icon
                })
              }
              if (item.fieldList) {
                item.fields = item.fieldList.map(field => {
                  return field.fieldname
                }).join('、')
              }
              return true
            }
            return false
          })
        }
      })
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=4&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    showAll () {
      if (!this.isShowAll) {
        this.isShowAll = true
        this.special.desc = this.tempDesc
      } else {
        this.isShowAll = false
        this.special.desc = this.tempDesc.substr(0, this.descLength) + '...'
      }
      setTimeout(() => {
        this.$refs.scroll.refresh()
      }, 50)
    },
    closeMask () {
      this.showGuideMask = false
    },
    refresh () {
      this.$refs.scroll.refresh()
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .share-special-detail
    background: #ffffff
    .guide-open-browser
      position: fixed
      left: 0
      top: 0
      right: 0
      bottom: 0
      z-index: 1000
      opacity: 0.7
      background: #000000
      .guide-img
        width: 5.12rem
        height: 2.54rem
        margin: 0.48rem auto 0
        bg-image('./bg_guide')
        background-size: 100% 100%
        background-repeat: no-repeat
    .container
      padding-bottom: 1.7rem
      .special-top
        position: relative
        box-sizing: border-box
        height: 3.8rem
        padding-top: 1.08rem
        text-align: center
        .top-bg
          position: absolute
          left: 0
          top: 0
          width: 100%
          height: 100%
          z-index: -1
        .top-logo
          display: block
          width: 1.12rem
          height: 1.12rem
          border-radius: 0.08rem
          margin: 0 auto 0.28rem
        .special-msgs
          position: absolute
          bottom: 0
          width: 100%
          height: 0.74rem
          opacity: 0.9;
          background-image: linear-gradient(-180deg, rgba(0,0,0,0.01) 0%, #000000 100%);
          color: #FFFFFF
          h1
            position: absolute
            top: 0
            left: 0
            padding-left: 0.24rem
            font-family: PingFangSC-Medium
            font-size: 0.32rem
            font-weight: bold
            line-height: 0.74rem
            max-width: 70%
            no-wrap()
          h3
            position: absolute
            top: 0
            right: 0.24rem
            font-size: 0.24rem
            color: #FFFFFF
            letter-spacing: -0.2px
            line-height: 0.74rem
      .special-desc
        padding: 0.3rem
        background: #ffffff
        font-size: 0.28rem
        line-height: 0.4rem
        color: #404040
        border-bottom: 0.1rem solid #f6f6f6
        word-break: break-all
        .special-show-btn
          font-size: 0.24rem
          color: #4D95FF
          letter-spacing: -0.08px
          line-height: 0.34rem
      .special-project-list
        padding: 0 0.3rem
        .item
          position: relative
          padding: 0.28rem 0 0.28rem 0.84rem
          border-bottom: 0.5px solid #EEEEEE
          &:last-child
            border-bottom: none
          .logo
            position: absolute
            left: 0
            top: 0.32rem
            width: 0.64rem
            height: 0.64rem
            border-radius: 0.08rem
          .name-wrapper
            font-size: 0
            span
              vertical-align: middle 
            .name
              font-family: PingFangSC-Medium
              font-size: 0.32rem
              font-weight: bold
              color: #333333
              letter-spacing: 0.54px
              line-height: 0.44rem
            .new
              display: inline-block
              width: 0.34rem
              height: 0.28rem
              margin-left: 0.06rem
              bg-image('./bg_new')
              background-size: 100% 100%
              background-repeat: no-repeat
            .money
              font-family: DIN-Medium
              font-size: 0.28rem
              color: #FF897D;
              letter-spacing: -0.09px;
              line-height: 0.34rem
          .desc
            font-family: PingFangSC-Regular
            font-size: 0.22rem
            color: #A7A7A7
            letter-spacing: -0.07px
            line-height: 0.34rem
            padding: 0.02rem 0 0.12rem
          .msgs
            font-size: 0
            .turn, .field
              font-family: PingFangSC-Regular
              font-size: 0.24rem
              color: #666666
              letter-spacing: -0.07px
              line-height: 0.34rem
            small 
              font-size: 0.22rem
              color: #C9C9C9
              margin: 0 0.1rem
              vertical-align: super
            .identify
              .badge
                height: 0.28rem
                margin-right: 0.1rem
                &:last-child
                  margin-right: 0
    .footer-open
      position: fixed
      left: 0
      bottom: 0
      right: 0
      height: 1.2rem
      border-top: 0.5px solid #f3f3f3
      background: #ffffff
      .logo
        position: absolute 
        left: 0.46rem
        top: 0.2rem
        height: 0.8rem
        width: 0.8rem
        border-radius: 0.08rem;
      h4
        position: absolute 
        left: 1.42rem
        top: 0.16rem
        font-family: PingFangSC-Medium
        font-size: 0.32rem
        font-weight: bold
        color: #41403E
        line-height: 0.44rem
      p
        position: absolute 
        left: 1.42rem
        bottom: 0.2rem
        font-family: PingFangSC-Regular
        font-size: 0.26rem
        color: #8B8B8B
        line-height: 0.36rem
      .btn
        position: absolute
        top: 0.32rem
        right: 0.22rem
        width: 1.56rem
        height: 0.6rem
        line-height: 0.6rem
        background: #FFC131
        border-radius: 0.04rem
        font-family: PingFangSC-Regular
        font-size: 0.3rem
        color: #41403E
        letter-spacing: -0.3px
        text-align: center
</style>


